import * as React from 'react';
import axios from 'axios';
import Button from '@mui/material/Button';
import Alert from '@mui/material/Alert';
import Snackbar from '@mui/material/Snackbar';
import { useState } from 'react';
import { useCookies } from 'react-cookie';
import { useNavigate } from 'react-router-dom';

export default function MemberDeleteBtn(_id) {
    const navigate = useNavigate();
    const navigateToMainTab = () => {
        navigate('/maintab');
    };
    const [alert, setAlert] = useState(false);
    const [error, setError] = useState(false);
    const logoutBtn = async () => {
        removeCookie('id');
        await axios
            .post('http://3.38.103.20:8000/auth/logout')
            .then((userData) => console.log(userData))
            .catch((error) => console.log(error));

        navigateToMainTab();
    };
    const [cookies, , removeCookie] = useCookies([]);
    const id = cookies.id;
    const deleteEvent = async () => {
        if (window.confirm(' ')) {
            // alert(' .');
            setAlert(true);
            await axios
                .delete(`http://3.38.103.20:8000/users/${id}`)
                .then((response) => console.log('delete', response));
            logoutBtn();
            // window.location.reload();
        } else {
            // alert('.');
            setError(true);
        }
    };

    return (
        <>
            <Button
                variant="contained"
                color="background"
                onClick={deleteEvent}
                style={{
                    borderRadius: 17,
                    border: '1px solid red',
                    color: 'red',
                    width: '200px',
                    height: '21px',
                    fontSize: '11px',
                    padding: '4px 14px',
                    gap: '10px',
                    marginTop: '30px'
                }}
            >
                 
            </Button>
            <Snackbar open={alert} autoHideDuration={1000}>
                <Alert severity="error" sx={{ width: '100%' }}>
                     .
                </Alert>
            </Snackbar>
            <Snackbar open={error} autoHideDuration={1000}>
                <Alert severity="warning" sx={{ width: '100%' }}>
                      .
                </Alert>
            </Snackbar>
        </>
    );
}
