import * as React from 'react';
import { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import Typography from '@mui/material/Typography';
import Box from '@mui/material/Box';
import TextField from '@mui/material/TextField';
import Button from '@mui/material/Button';
import Alert from '@mui/material/Alert';
import Snackbar from '@mui/material/Snackbar';

export default function CreateUser() {
    const [alert, setAlert] = useState(false);
    const [emailAlert, setEmailAlert] = useState(false);
    const [pwAlert, setPwAlert] = useState(false);
    const [ID, setID] = useState('');
    const [email, setEmail] = useState('');
    const [pw, setPw] = useState('');
    const [repw, setrePw] = useState('');
    const validEmail = '^[a-zA-Z0-9+-_.]+@[a-zA-Z0-9-]+.[a-zA-Z0-9-.]+$';
    const navigate = useNavigate();
    const handleClose = (event, reason) => {
        setAlert(false);
        setEmailAlert(false);
        setPwAlert(false);
    };
    const nextBtn = () => {
        if (ID === '' || email === '' || pw === '') {
            // alert(' ');
            setAlert(true);
        } else if (email.match(validEmail) == null) {
            // alert('   ');
            setEmailAlert(true);
        } else if (pw !== repw) {
            // alert('  ');
            setPwAlert(true);
        } else {
            navigate('/maintab/member/createuser2', { state: { id: ID, pw: pw, email: email } });
        }
    };

    return (
        <>
            <userForm>
                <Box
                    component="form"
                    sx={{
                        '& > :not(style)': { m: 1, width: '25ch' }
                    }}
                    noValidate
                    autoComplete="off"
                    style={{
                        display: 'flex',
                        flexDirection: 'column',
                        justifyContent: 'center',
                        alignItems: 'center'
                    }}
                >
                    {' '}
                    <Typography
                        variant="h6"
                        textAlign={'center'}
                        style={{ fontWeight: 700, fontSize: '1rem' }}
                    >
                        
                    </Typography>
                    <TextField
                        id="ID"
                        label=" "
                        variant="standard"
                        onChange={(e) => setID(e.target.value)}
                    />
                    <TextField
                        id="email"
                        label=" "
                        variant="standard"
                        onChange={(e) => setEmail(e.target.value)}
                    />
                    <TextField
                        id="pw"
                        label=" "
                        variant="standard"
                        onChange={(e) => setPw(e.target.value)}
                    />
                    <TextField
                        id="repw"
                        label=" "
                        variant="standard"
                        onChange={(e) => setrePw(e.target.value)}
                    />
                    <Button
                        variant="contained"
                        onClick={nextBtn}
                        style={{
                            display: 'flex',
                            borderRadius: '99px',
                            width: '312px',
                            height: '44px',
                            marginTop: '150px'

                            // left: '24px',
                            // top: '635px'
                        }}
                    >
                        
                    </Button>
                </Box>
            </userForm>
            <Snackbar open={alert} autoHideDuration={700} onClose={handleClose}>
                <Alert severity="error" sx={{ width: '100%' }}>
                     .
                </Alert>
            </Snackbar>
            <Snackbar open={emailAlert} autoHideDuration={700} onClose={handleClose}>
                <Alert severity="error" sx={{ width: '100%' }}>
                       .
                </Alert>
            </Snackbar>
            <Snackbar open={pwAlert} autoHideDuration={700} onClose={handleClose}>
                <Alert severity="error" sx={{ width: '100%' }}>
                      .
                </Alert>
            </Snackbar>
        </>
    );
}
