import * as React from 'react';
import styled from '@emotion/styled';

import SKKUD from '../../../assets/SKKUD_green.jpeg';

const StyledCard = styled.div`
    box-sizing: border-box;
    border: 1px solid white;
    border-radius: 13px;
`;

const TitleAbout = styled.div`
    font-size: 2.5rem;
    font-weight: 700;
    line-height: 3rem;
    justify-content: 'end';
    margin-top: 31px;
    margin-left: 19px;
`;

const Detail1 = styled.div`
    display: 'block';
    width: 100%;
    font-size: 0.875rem;
    font-weight: 700;
    line-height: 1.438rem;
    margin-top: 27px;
    margin-left: 24px;
`;

const Detail2 = styled.div`
    display: 'block';
    width: 100%;
    font-size: 0.813rem;
    line-height: 1.438rem;
    font-weight: 400;
    margin-top: 13px;
    margin-left: 24px;
    margin-bottom: 30px;
`;
export default function About() {
    return (
        <StyledCard>
            <TitleAbout>About</TitleAbout>
            <img
                src={SKKUD}
                alt="SKKUD"
                style={{
                    marginLeft: 'auto',
                    display: 'block',
                    marginRight: '21px',
                    width: '213px'
                }}
            />
            <Detail1>
                      
                <br />    .
            </Detail1>
            <Detail2>
                    
                <br />       .
                <br />       
                <br />
                 .
            </Detail2>
        </StyledCard>
    );
}
